      </div>
    </div>
    <div class="audio-player-list" v-if="!mini && list && list.length > 1">
      <ul>
        <li v-for="(media, index) in list" :class="{ 'active': media === value }" :key="index" @click="changeMedia(media)">
          {{media.name}}
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import slider from './slider.vue'
/**
 * @see https://docs.microsoft.com/en-us/windows/win32/wmp/object-model-reference-for-scripting
 */
export default {
  name: 'player-ie',
  props: {
    /**
     * 音频文件数组
     */
    list: {
      type: Array,
    },
    /**
     * 是否可最小化，默认否
     */
    miniable: {
      type: Boolean,
      default: false,
    },
    /**
     * 默认倍速选项
     */
    rates: {
      type: Array,
    }
  },
  components: {
    slider
  },
  data () {
    return {
      playBackRate: 1, // 默认倍速
      id: `player${(Math.random(1) * 1000).toFixed(0)}`,
      mini: false,
      muted: false,
      volume: 1,
      rate: 0,
      error: false,
      playing: false,
      currentTime: '',
      duration: '',
      timer: '',
      value: {},
      player: ''
    }
  },
  created () {
    this.value = this.list[0]
  },
  mounted () {
    this.player = document[this.id]
  },
  watch: {
    list (val) {
      if (val && val.length) {
        this.changeMedia(val[0])
      }
    }
  },
  beforeDestory () {
    // console.log('beforeDestory触发停止播放')
    this.closePlayer()
  },
  methods: {
    /**
     * 播放器关闭时，暂停录音并清空播放器
     * 兼容处理了ie和其他浏览器
     */
    closePlayer () {
      // console.log('关闭触发停止播放')
      this.pause()
      this.player = ''
    },
    /**
     * 回到常规模式
     */
    changeStatus (val) {
      this.mini = val
      this.$emit('change', val)
    },
    /**
     * 关闭
     */
    close () {
      this.$emit('close')
    },
    /**
     * 播放
     */
    play () {
      if (!this.id) {
        this.error = true
        return
      }
      this.player.url = this.value.src
      this.player.settings.volume = this.volume * 100 // 初始化音量
      this.player.settings.rate = this.playBackRate // 初始化倍速
      this.timer = window.setInterval(() => {
        this.duration = this.player && (this.player.currentMedia.durationString || '--:--')
        this.currentTime = this.player && (this.player.controls.currentPositionString || '00:00')
        this.rate = this.player && (this.player.controls.currentPosition / this.player.currentMedia.duration)
      }, 1000)
      this.player.controls.play()
      this.playing = true
    },
    /**
     * 暂停
     */
    pause () {
      this.playing = false
      this.player && this.player.controls.pause()
      clearInterval(this.timer)
    },
    /**
     * 切换播放和暂停
     */
    togglePlay () {
      if (this.playing) {
        this.pause()
      } else {
        this.play()
      }
    },
    /**
     * 调整播放进度
     * @param {number} rate 进度比例
     */
    changeTime (rate) {
      if (this.player) {
        this.player.controls.currentPosition = this.player.currentMedia.duration * rate
      }
    },
    /**
     * 调整倍速
     * @param {number} rate 倍速
     */
    changeRate (rate) {
      this.player.settings.rate = rate
      this.playBackRate = rate // 记录变更的倍速
    },
    /**
     * 调整音量
     */
    changeVolume (val) {
      this.player.settings.volume = val * 100
      this.mute(val === 0)
    },
    /**
     * 调整音量
     */
    mute (val) {
      this.muted = val
      this.player.settings.mute = val
    },
    /**
     * 切换静音
     */
    toggleMute () {
      this.mute(!this.muted)
    },
    /**
     * 切换音频
     * @param {object} media 音频对象
     */
    changeMedia (media) {
      if (media === this.value) {
        this.togglePlay()
      } else {
        this.pause()
        this.rate = 0
        this.duration = ''
        this.currentTime = '00:00'
        this.value = media
        this.$nextTick(() => {
          this.play()
        })
      }
    },
  },
}
</script>
